import service from '../../service.js';
import headers from '@/components/header_top.vue';
export default {
	components: {
		headers
	},
	data() {
		return {
			passwords:'',
			emailCode:'',
			email:'',
			tabIndex: 0,
			navList: [
				{
					text: '手机号解绑',
					state: '0'
				},
				{
					text: '邮箱号解绑',
					state: '1'
				}
			],
			title: '',
			icon: '../../static/icon/left_whitch.png',
			url_left: '/pages/login/login',
			// subtitle: '',
			countdown: 62,
			checked: true,
			mobile: '',
			password: '',
			verifycode: ''
		};
	},
	methods: {
		// back() {
		// 	uni.navigateTo({
		// 		url: '/pages/login/login'
		// 	})
		// },
		//swiper 切换
		changeTab(e){
			this.tabClick(e.target.current);
		},
		
		//顶部tab点击
		tabClick(index){
			this.change(index);
		},
		change(index){
			this.type = this.navList[index].type;
			this.tabIndex = index;
		},
		jump(url) {
			uni.navigateTo({
				url: url
			});
		},
		// 手机号验证码
		send() {
			var self = this;
			uni.request({
				url: service.api.reg.code,
				method: 'GET',
				data: {
					mobile: this.mobile,
					temp: 'sms_unbind'
				},
				success: res => {
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						self.codetime(self, 60);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						return;
					}
				}
			});
			// this.codetime(this,60);
		},
		
		// 邮箱验证码
		emailCodes(){
			var self = this;
			uni.request({
				url: service.api.reg.sendEmailCode,
				method: 'GET',
				data: {
					email: this.email,
				},
				success: res => {
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						self.codetime(self, 60);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						return;
					}
				}
			});
		},
		//手機驗證碼獲取倒計時
		codetime: function(obj, num) {
			if (num < 10) {
				obj.countdown = '0' + num;
			} else {
				obj.countdown = num;
			}
			num--;
			if (num < 0) {
				obj.countdown = 61;
			} else {
				setTimeout(function() {
					obj.codetime(obj, num);
				}, 1000);
			}
		},
		submit() {
			var self = this;
			if(!(/^1[3-9][0-9]\d{8,11}$/.test(self.mobile))){
				uni.showToast({
					icon: 'none',
					title: '手机号码格式不正确'
				});
				return;
			}
			if (self.password.length < 8 && !(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,}$/.test(self.password))) {
				uni.showToast({
					icon: 'none',
					title: '请输入数字与字母结合的八位数以上登录密码'
				});
				return;
			}
			uni.request({
				url: service.api.login.unbind,
				method: 'POST',
				data: {
					mobile: self.mobile,
					password: self.password,
					verifycode: self.verifycode,
				},
				success(res) {
					console.log(res.data);
					if (res.data.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/login/login'
							});
						}, 1000);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				}
			});
		},
		
		emailSubmit() {
			var self = this;
			if (!(/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,5}$/.test(self.email))) {
				uni.showToast({
					icon: 'none',
					title: '请输入合理的邮箱号!'
				});
				return;
			}												 
			if (self.passwords.length < 8 && !(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8}$/.test(self.passwords))) {
				uni.showToast({
					icon: 'none',
					title: '请输入数字与字母结合的八位数以上登录密码'
				});
				return;
			}
			uni.request({
				url: service.api.login.emailunbind,
				method: 'POST',
				data: {
					email: self.email,
					password: self.passwords,
					verifycode: self.emailCode,
				},
				success(res) {
					console.log(res.data);
					if (res.data.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/login/login'
							});
						}, 1000);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				}
			});
		}
	}
};
